___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
}
.container {
    display: grid;
    grid-template-columns: 2fr 3fr 3fr;
    grid-template-rows: 4fr 1fr
}

.cursorDisabled {
    cursor: not-allowed;
}
.buttons {
    grid-column: 3 / 4;
    display: grid;
    grid-template-columns: 1fr 1fr;
}
#start-btn, #reset-btn {
    height: 50px;
    width: 200px;
    margin: 20px;
}

#start-btn { grid-column: 1 / 2; }
#reset-btn { grid-column: 2 / 3; }

#my-board, #enemy-board {
    display: inline-grid;
    grid-template-rows: repeat(10, 1fr);
    grid-template-columns: repeat(10, 1fr);
    height: 70vh;
    width: 35vw;
    padding: 20px;
}
#harbour { 
    /* display: none; */
    height: 70vh;
    width: 25vw;
    padding: 20px;
}
.square {
    border: 1px solid black;
    background-color: #e5e7eb;
    position: relative;
}

.active {
    background-color: green;
}

.hit {
    background-color: red !important;
}

.miss {
    background-color: blue;
}

.ship {
    border: 2px solid black;
    display: inline-block;
    /* position: absolute; */
}

#ship1 {
    height: calc(65px * 4);
    width: 25px;
    /* grid-area: 4 / 1 / 8 / 2; */
}

#ship2 {
    height: calc(65px * 3);
    width: 25px;
    /* grid-area: 2 / 3 / 5 / 4; */
}

#ship3 {
    height: calc(65px * 3);
    width: 25px;

    /* grid-area: 4 / 5 / 7 / 6; */
}

#ship4 {
    height: calc(65px * 2);
    width: 25px;

    /* grid-area: 3 / 7 / 4 / 8; */
}

#ship5 {
    height: calc(65px * 2);
    width: 25px;

    /* grid-area: 5 / 9 / 6 / 10; */
}

#ship6 {
    height: calc(65px * 2);
    width: 25px;

    /* grid-area: 6 / 3 / 8 / 4; */
}

#ship7 {
    height: calc(65px * 1);
    width: 25px;

    /* grid-area: 6 / 7 / 8 / 8; */
}

#ship8 {
    height: calc(65px * 1);
    width: 25px;

    /* grid-area: 8 / 5 / 10 / 6; */
}

#ship9 {
    height: calc(65px * 1);
    width: 25px;

    /* grid-area: 7 / 9 / 8 / 10; */
}

#ship10 {
    height: calc(65px * 1);
    width: 25px;

    /* grid-area: 9 / 9 / 10 / 10; */
